$mobile: 767px;
$tablet: 1023px;
$desktop: 1024px;

/*,   767px */
@mixin mobile {
    @media (max-width: $mobile) {
        @content;
    }
}

/*,   768, 1023px */
@mixin tablet {
    @media (min-width: ($mobile+1)) and (max-width: $tablet) {
        @content;
    }
}

/*,   1024px */
@mixin desktop {
    @media (min-width: $desktop) {
        @content;
    }
}

#navbar {
    width: 100%;
    padding: 0 40px;
    box-sizing: border-box;
    height: 60px;
    display: flex;
    align-items: center;
    position: fixed;
    justify-content: center;
    z-index: 3;
}
#navbar-bg {
    background-color: white;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    opacity: 0.6;
}
#navbar-content {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include desktop {
        max-width: 1024px;
    }
    @mixin tablet {
        max-width: 768px;
    }
}
#navbar-brand {
    display: flex;
}
#navbar-brand img {
    height: 30px;
}
#navbar-brand .text {
    font-size: 22px;
    font-weight: bold;
    color: rgb(46, 97, 230);
    position: relative;
    top: 3px;
    display: flex;
}

#navbar-menu {
    display: flex;
    height: 100%;

    .nav-item {
        font-weight: 700;
        font-size: 1rem;
        text-decoration: none;
        color: rgb(46, 97, 230);
        /* margin-left: 40px; */
        padding: 20px;
        cursor: pointer;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        box-sizing: border-box;
        position: relative;
        .drop {
            user-select: none;
            pointer-events: none;
            position: absolute;
            top: 60px;
            overflow: hidden;
            border-radius: 0 0 5px 5px;

            div {
                transition: 0.3s;
                transform: translate(0, -105%);
                width: 200px;
                display: flex;
                flex-direction: column;
                align-items: center;
                background: rgba(255, 255, 255, 0.89);
                color: rgb(46, 97, 230);
                pointer-events: all;
                li {
                    color: rgb(46, 97, 230);
                    padding: 20px;
                    width: 100%;
                    display: flex;
                    box-sizing: border-box;
                    align-items: center;
                    justify-content: center;
                    text-align: center;
                    &:hover {
                        background: rgb(46, 97, 230);
                        color: white;
                        transition: 0.2s;
                        a {
                            text-decoration: none;
                            color: white;
                        }
                    }
                    a {
                        color: rgb(46, 97, 230);
                    }
                }
            }
        }

        &::after {
            content: "";
            width: 100%;
            position: absolute;
            height: 5px;
            background-color: rgb(46, 97, 230);
            bottom: 0;
            opacity: 0;
            transition: 0.3s;
            border-radius: 5px 5px 0 0;
        }

        &:hover {
            &::after {
                opacity: 1;
            }
            .drop {
                div {
                    transform: translate(0, 0);
                }
            }
        }
    }
}
