.home-container {
	width: 100%;
	// height: 2500px;
	overflow: hidden;
	display: flex;
	justify-content: flex-start;
	position: relative;
	align-items: center;
	flex-direction: column;
	.bg-img {
		object-fit: cover;
		width: 103%;
		height: 102vh;
		filter: grayscale(50%) blur(2px) contrast(120%);
		margin-top: -10px;
		object-position: top;
		position: fixed;
	}
}
.shape-container {
	display: flex;
	align-items: center;
	flex-direction: column;
	width: 100%;
	height: 100vh;
	justify-content: center;
	max-width: 1024px;
	text-shadow: 1px 1px 1px rgb(65, 65, 65);
	z-index: 2;
	img {
		width: 40%;
		filter: drop-shadow(1px 1px 1px rgb(65 65 65));
	}
	span.text {
		font-weight: 700;
		color: white;
		font-size: 32px;
		margin-top: 7px;
		display: flex;
		p {
			color: #2c60e4;
			margin-left: 8px;
		}
	}
	span.desc {
		color: white;
		text-align: center;
		font-weight: 500;
		font-size: 19px;
		margin-top: 15px;
		line-height: 1.3;
	}
}

.center-logo {
	height: 10%;
	transform: none;
	transform: translate3d(0, 0, 1000px);
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	margin: auto;
	text-align: center;
}
.slides-title {
	position: absolute;
	bottom: 0;
	width: 100%;
	background: black;
	height: 40px;
	opacity: 0.8;
	font-size: 22px;
	display: flex;
	align-items: center;
	justify-content: center;
	color: white;
}

.contact-button {
	--btnWidth: 150px;
	position: relative;
	overflow: hidden;
	font-size: 20px;
	padding: 15px;
	width: var(--btnWidth);
	height: 40px;
	border: 1px solid #2c60e4;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 5px;
	margin-top: 50px;
	height: 55px;
	span {
		position: absolute;
		display: block;
		width: 0;
		height: 0;
		border-radius: 50%;
		background-color: #2c60e4;
		transition: 0.4s ease-in-out;
		transform: translate(-50%, -50%);
		z-index: 2;
	}

	p {
		color: #2c60e4;
		z-index: 3;
		position: absolute;
		transition: 0.5s ease-in-out;
	}
	&:hover {
		span {
			width: calc(var(--btnWidth) * 2.5);
			height: calc(var(--btnWidth) * 2.5);
		}
		p {
			color: white;
		}
	}
}

.btn-6:active {
	background-color: #af0d33;
}
.container-body {
	width: 100%;
	z-index: 2;
	padding: 5px;
	box-sizing: border-box;
}
.swiper-toggle {
	font-size: 18px;
	margin-right: 5px;
	margin-left: 15px;
}
.swiper-toggle.toggle-close + .swiper-area + .card {
	display: none;
}
.card {
	width: 100%;
}

.swiper-area {
	display: flex;
	.swiper-title {
		color: white;
		font-size: 28px;
		display: flex;
		align-items: center;
		justify-content: flex-start;
		padding: 5px;
		margin-left: 5px;

		.solution-title {
			margin-left: 10px;
			margin-right: 10px;
			color: white;
			background-color: none;
			background: #00000061;
			border: none;
			font-size: 30px;
			width: 350px;
			text-align: left;
			border-radius: 5px;
		}
		.project-title {
			margin-right: 10px;
		}
		.p-dropdown {
			margin-left: 10px;
			font-size: 15px;
		}
		.add-project-btn,
		.rm-solution-btn {
			margin-left: 10px;
			height: 40px;
		}
	}
}
.solution-location-change-area {
	display: flex;
    flex-direction: column;
    height: 50px;
    justify-content: space-between;
	button {
		font-size: 12px;
		height: 40%;
		padding: 0;
	}
}

.p-dropdown-label {
	padding: 10px !important;
	display: flex;
	justify-content: center;
	align-items: center;
}
.p-dropdown-item {
	font-size: 15px !important;
	text-align: center;
	padding: 10px 0 !important;
}
.add-solution-btn {
	margin-top: 15px;
}
.solution-area {
	margin-bottom: 20px;
}
.modify-solution-area {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	align-content: center;
	align-items: center;
	background: #ffffff69;
	border-radius: 5px;
	padding: 10px;
	box-sizing: border-box;
	margin-bottom: 15px;
	.hover-button {
		opacity: 0;
		transition: 0.3s;
	}
	&:hover {
		.hover-button {
			opacity: 1;
		}
	}
}
.swiper {
	overflow: inherit !important;
	height: 400px;
	--hover-hight: 15px;
	&:hover {
		.swiper-button-area {
			.swiper-button-prev,
			.swiper-button-next {
				opacity: 1;
			}
		}
	}
	.swiper-button-area {
		position: absolute;
		width: 100%;
		height: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		top: 0;
		z-index: 3;
		padding: 30px;
		box-sizing: border-box;
		user-select: none;
		pointer-events: none;
		.swiper-button-prev,
		.swiper-button-next {
			box-shadow: rgba(0, 0, 0, 0.15) 0px 4px 10px;
			transition: 0.4s ease-in-out;
			opacity: 0;
			box-sizing: border-box;
			/* padding: 10px; */
			width: 50px;
			height: 50px;
			padding: 10px;
			border-radius: 50%;
			border: none;
			cursor: pointer;
			box-shadow: rgba(0, 0, 0, 0.15) 0px 4px 10px;
			img {
				height: 100%;
				width: 100%;
				object-fit: cover;
			}
			&:hover {
			}
			pointer-events: all;
		}
	}

	.swiper-slide {
		position: relative;
		padding: 10px;
		box-sizing: border-box;
		cursor: pointer;

		&:hover {
			.swiper-slide-cont {
				margin-top: calc(-1 * var(--hover-hight));
				.bg-gradient {
					.slide-cont-title {
						bottom: var(--hover-hight);
					}
				}
			}
		}

		.swiper-slide-cont {
			transition: 0.4s;
			margin-top: 0;
			border-radius: 10px;
			background-color: #ffffff42;
			width: 100%;
			height: 100%;
			background-position: center;
			background-size: cover;
			overflow: hidden;
			display: block;
			box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
			img {
				width: 100%;
				height: 100%;
			}
			.bg-gradient {
				position: relative;
				width: 100%;
				height: 100%;
				background: linear-gradient(to top, rgba(0, 0, 0, 0.5), transparent);
				.slide-cont-title {
					position: absolute;
					bottom: 0;
					color: white;
					font-weight: 800;
					box-sizing: border-box;
					font-size: 21px;
					padding: 11px;
					width: 100%;
					display: flex;
					justify-content: center;
					transition: 0.4s;
				}
			}
		}
	}
}
.p-datatable-table {
	margin-top: 15px;
	border-radius: 5px;
	overflow: hidden;
}
.swiper.wide {
	.swiper-slide {
		height: 400px;
	}
}

.swiper.grid {
	height: 600px;

	.swiper-slide {
		height: calc((100% / 2) - 30px) !important;
	}
}

.p-datatable-thead {
	font-size: 20px;
	font-weight: 600;
}
.p-datatable-thead > tr > th {
	padding: 15px;
	pointer-events: none;
	vertical-align: middle;
}

.p-datatable-tbody > tr > td {
	padding: 10px 15px;
	vertical-align: middle;
	.delete-btn-area {
		display: flex;
	}
	svg.p-icon {
		width: 20px;
		height: 20px;
	}
	.p-inputtext {
		width: 100%;
		padding-left: 5px;
		margin-left: -6px !important;
		margin-top: -2px !important;
	}
}
.project-img-area {
	height: 90px;
	position: relative;

	img {
		width: 100%;
		height: 100%;
		object-fit: contain;
	}
}
.project-img-area.modifyMode {
	&::after {
		cursor: pointer;
		content: "Drop \aor\a click";
		white-space: pre;
		/* display: flex; */
		text-align: center;
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.0784313725);
		left: 0;
		top: 0;
		padding: 15px;
		box-sizing: border-box;
		font-size: 20px;
		color: white;
		text-shadow: 4px 2px 4px black;
	}
}

.cursor {
	width: 1em;
	height: 1em;
	position: fixed;
	background: #fff;
	border-radius: 50%;
	transform: translate(-50%, -50%);
	top: calc(var(--y) * 1px);
	left: calc(var(--x) * 1px);
	mix-blend-mode: difference;
	pointer-events: none;
	cursor: none;
	z-index: 999;
	// transition: top 100ms ease-out, left 100ms ease-out;
}
